export default {
	data() {
		return {
			couponList: []
		}
	},
	onLoad(params) {
		let _this = this;
		let userInfo = uni.getStorageSync("userInfo");
		_this.request({
			url: _this.http.getRecevieCouponList,
			data: {
				pageNum: '1',
				pageSize: '100',
				isSand: 1
			},
			success: (res) => {
				if (res.code === '0') {
					_this.couponList = res.result;
				}
			}
		});
	},
	methods: {
		getCoupon(data) {
			let _this = this;
			let userInfo = uni.getStorageSync('userInfo');
			_this.request({
				url: _this.http.addUserCoupon,
				data: {
					couponId: data.couponId,
					userId: userInfo.userId
				},
				success: (res) => {
					if (res.code === '0') {
						uni.showToast({
							title: '获取成功',
							duration: 2000,
							icon: "success",
							mask: true
						});
					}else{
						uni.showToast({
							title: '获取失败',
							duration: 2000,
							mask: true
						});
					}
				}
			});
		}
	}
}
